.header-hide{width:100%;height:10vh;background-color:#5F4B8BFF;position:fixed;top:0;z-index:5}@media (max-width: 920px){.header-hide{-webkit-transition:all 0.5s ease-out;transition:all 0.5s ease-out}}.header-show{height:100%;width:100%;background-color:#5F4B8BFF;position:fixed;z-index:55}@media (max-width: 920px){.header-show{-webkit-transition:all 0.5s ease-out;transition:all 0.5s ease-out}}.btn{visibility:hidden;position:absolute;right:20px;top:20px}@media (max-width: 920px){.btn{visibility:visible}}.btn .line{width:30px;height:5px;background-color:#E69A8DFF;margin:5px 0}.btn .crossed{width:30px;height:5px;background-color:#E69A8DFF;margin:5px -10px 5px 0}.btn .crossed:nth-child(1){-webkit-transform:rotate(-45deg) translate(-8px, -2px);transform:rotate(-45deg) translate(-8px, -2px)}.btn .crossed:nth-child(2){-webkit-transform:rotate(45deg) translate(-9px, 0);transform:rotate(45deg) translate(-9px, 0)}.navigation-links{width:25%;height:100%;margin:0 auto;display:flex;justify-content:space-evenly;align-items:center}@media (max-width: 920px){.navigation-links{display:none}}.navigation-links a{list-style-type:none;font-size:23px;color:#E69A8DFF;margin:0 20px;text-decoration:none}.navigation-links-mobile{width:100%;flex-direction:column;justify-content:space-evenly;align-items:center;height:50%;margin:0 auto;display:flex}.navigation-links-mobile a{list-style-type:none;font-size:23px;color:#E69A8DFF;margin:0 20px;text-decoration:none}.about-container{width:100%;height:100vh;padding-top:22vh;box-sizing:border-box}.about-container hr{width:50vw;background-color:#E69A8DFF;height:3px;border-radius:3px}@media (max-width: 920px){.about-container hr{display:none}}.about,.skills{width:70vw;margin:40px auto}h1{font-size:25px;font-family:'Merriweather', serif}p{font-size:20px}.skills-list{display:flex;justify-content:center;list-style-type:none;padding:0px 20px;box-sizing:border-box}.skills-list li{margin:30px auto}@media (max-width: 585px){.skills-list{flex-wrap:wrap}.skills-list li{width:50px;margin:0px}}@media (max-width: 920px){.about,.skills{width:100%;padding:0 20px;box-sizing:border-box;margin:0px auto}h1{font-size:24px}p{font-size:16px}.skills-list li{margin:10px auto}}@media (max-width: 585px){.skills-list li{margin:5px auto}}@media (max-width: 390px){h1{font-size:20px}p{font-size:14px}.skills-list li{margin:0px auto}}.projects-container{width:100%;padding-top:10vh;box-sizing:border-box}.projects-container h1{margin-bottom:50px}.projects-container hr{width:50vw;background-color:#E69A8DFF;height:3px;border-radius:3px;margin:50px auto}.project{width:100%;display:flex;justify-content:space-around;align-items:center}@media (max-width: 920px){.project{flex-direction:column;justify-content:center}}.project-image{width:45%;box-shadow:0px 0px 11px 0px #8a8989}@media (max-width: 920px){.project-image{width:100%;box-shadow:none;padding:0 30px;box-sizing:border-box}}#image{width:100%;margin-bottom:-5px}.project-info{width:45%;text-align:left}@media (max-width: 920px){.project-info{width:100%;padding:0 40px;box-sizing:border-box}}.project-info .external-links{display:flex;align-items:center;justify-content:center}.App{position:relative;width:100%;text-align:center;font-family:"Montserrat", sans-serif;margin:0px;background-color:#fcede7;color:#5F4B8BFF}.home{height:100vh;width:100%}.home hr{width:50vw;background-color:#E69A8DFF;height:3px;border-radius:3px}.landing{margin:0 25vh;padding:30vh 0;box-sizing:border-box}.landing h1{font-size:3em;font-family:"Merriweather", serif;-webkit-animation:swoop 1s ease-in;animation:swoop 1s ease-in;color:#5F4B8BFF}.landing p{font-size:23px;margin-top:-35px}.options{margin:0 auto;display:flex;flex-direction:row;justify-content:center;align-items:center;cursor:pointer}.options a{margin:0 10px;text-decoration:none}@-webkit-keyframes swoop{0%{-webkit-transform:scale(1.1, 1.1) translateY(-150%);transform:scale(1.1, 1.1) translateY(-150%)}100%{-webkit-transform:scale(1, 1) translateY(0%);transform:scale(1, 1) translateY(0%)}}@keyframes swoop{0%{-webkit-transform:scale(1.1, 1.1) translateY(-150%);transform:scale(1.1, 1.1) translateY(-150%)}100%{-webkit-transform:scale(1, 1) translateY(0%);transform:scale(1, 1) translateY(0%)}}#up{position:fixed;bottom:100px;right:20px}@media (max-width: 920px){#up{display:none}}footer{width:100%;height:10vh;background-color:#5F4B8BFF;color:#E69A8DFF;margin-top:100px;display:flex;align-items:center;justify-content:center}@media (max-width: 920px){.landing{margin:0 5vh}.landing h1{font-size:28px;margin-bottom:40px}.landing p{font-size:20px}}

